
<div>
  <el-alert type="warning" title="virtual-column组件设type属性为 expand" show-icon></el-alert>
  <virtual-scroll
    ref="virtualScroll"
    :data="list"
    :item-size="62"
    key-prop="id"
    @change="onVirtualChange">
    <el-table
      ref="table"
      v-loading="loading"
      :data="tableData"
      height="600"
      row-key="id"
      style="width: 100%"
      :default-expand-all="false"
      :expand-row-keys="expandRowKeys"
      @expand-change="handleExpandChange">
      <virtual-column type="expand">
        <template slot-scope="props">
          <el-form label-position="left" class="demo-table-expand">
            <el-form-item label="商品名称">
              <span>{{ props.row.name }}</span>
            </el-form-item>
            <el-form-item label="所属店铺">
              <span>{{ props.row.shop }}</span>
            </el-form-item>
            <el-form-item label="商品 ID">
              <span>{{ props.row.id }}</span>
            </el-form-item>
            <el-form-item label="店铺 ID">
              <span>{{ props.row.shopId }}</span>
            </el-form-item>
            <el-form-item label="商品分类">
              <span>{{ props.row.category }}</span>
            </el-form-item>
            <el-form-item label="店铺地址">
              <span>{{ props.row.address }}</span>
            </el-form-item>
            <el-form-item label="商品描述">
              <span>{{ props.row.desc }}</span>
            </el-form-item>
            <el-form-item label="商品描述">
              <span>{{ props.row.desc }}</span>
            </el-form-item>
            <el-form-item label="商品描述">
              <span>{{ props.row.desc }}</span>
            </el-form-item>
            <el-form-item label="商品描述">
              <span>{{ props.row.desc }}</span>
            </el-form-item>
          </el-form>
        </template>
      </virtual-column>
      <el-table-column
        label="商品 ID"
        prop="id">
      </el-table-column>
      <el-table-column
        label="商品名称"
        prop="text">
      </el-table-column>
      <el-table-column
        label="描述"
        prop="text2">
      </el-table-column>
    </el-table>
  </virtual-scroll>
  <div style="margin-top: 20px">
    <el-button @click="toggleExpand([list[1], list[2]])">切换第二、第三行的展开收起状态</el-button>
    <el-button @click="toggleExpand(list, false)">全部收起</el-button>
    <el-button @click="setExpandRowKeys()">setExpandRowKeys[4, 5, 6, 7]</el-button>
    <el-button @click="insert()">insert</el-button>
  </div>
</div>
